.spinner {
	position: relative;
	width: var(--size);
	height: var(--size);
	margin: 0 auto;
}

.spinner__ring {
	width: var(--ring-size);
	height: var(--ring-size);
	position: absolute;
	margin: 8px;
	border-width: 8px;
	border-style: solid;
	border-radius: 50%;
	animation: spinner 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
	border-color: var(--brand-color-100) transparent transparent transparent;
}

.spinner__ring:nth-child(1) {
	animation-delay: -0.45s;
}

.spinner__ring:nth-child(2) {
	animation-delay: -0.3s;
}

.spinner__ring:nth-child(3) {
	animation-delay: -0.15s;
}

@keyframes spinner {
	0% {
		transform: rotate(0deg);
	}

	100% {
		transform: rotate(360deg);
	}
}